import React, {Component} from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGithub,
  faLinkedin,
  faInstagram,
} from "@fortawesome/free-brands-svg-icons";
export default class Contact extends Component{
    render(){
        return (
            <div className="container">
                <hr style={{color: "#B8B8B8", border: "0.5px solid black" }}/>
                <h4>Contact</h4>
                <hr style={{color: "#B8B8B8", width: "200px"}}/>
                <div className="row">
                    <div className="col-12">
                        {/* For spacing between icon I'm using bootsrtap spacing property i.e className="mx-2" */}
                        <a href="https://github.com/ameyzing09">
                            <FontAwesomeIcon color="black" className="mx-2" icon={faGithub}/>
                        </a>
                        <a href="https://www.instagram.com/ameykode01/">
                            <FontAwesomeIcon color="black" className="mx-2" icon={faInstagram}/>
                        </a>
                        <a href="https://www.linkedin.com/in/amey-kode-b0898b2a/">
                            <FontAwesomeIcon color="black" className="mx-2" icon={faLinkedin}/>
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}