import React, {Component} from 'react'

export default class Education extends Component{
    render(){
        return (
            <div className="container">
                <hr style={{color: "#B8B8B8", border: "0.5px solid black" }}/>
                <h4>Education</h4>
                <hr style={{color: "#B8B8B8", width: "200px"}} />

                <div className="row">
                    <div className="col-3 text-justify">
                        <h6 style={{color: '#B8B8B8'}}>2018-2021</h6>
                    </div>
                
                    <div className="col-6 text-justify">
                        <p className="para">
                            <strong>P.V.G's College Of Science</strong>
                            <br />
                            B.Sc (Computer Science)
                            <br />
                            <em>Percentage : 68%</em>
                        </p>
                    </div>
                 </div>

                <div className="row">
                    <div className="col-3 text-justify">
                        <h6 style={{color: '#B8B8B8'}}>2012-2014</h6>
                    </div>
                
                    <div className="col-6 text-justify">
                        <p className="para">
                            <strong>M.E.S Boy's College</strong>
                            <br />
                            H.S.C 
                            <br />
                            <em>Percentage : 55.56%</em>
                        </p>
                    </div>
                </div>

                <div className="row">
                    <div className="col-3 text-justify">
                        <h6 style={{color: '#B8B8B8'}}>2000-2012</h6>
                    </div>
                
                    <div className="col-6 text-justify">
                        <p className="para">
                            <strong>Aranyeshwar English Medium School</strong>
                            <br />
                            S.S.C 
                            <br />
                            <em>Percentage : 67%</em>
                        </p>
                    </div>
                </div>
            </div>
        )
    }
}